import React from 'react';
import { makeStyles } from '@material-ui/core/styles';
import Paper from '@material-ui/core/Paper';
import Typography from '@material-ui/core/Typography';
import User from './images/user.png'
import MailIcon from '@material-ui/icons/Mail';
import PhoneIcon from '@material-ui/icons/Phone';
import LocationOnIcon from '@material-ui/icons/LocationOn';
import Facebook from './images/fb.png'
import LinkedIn from './images/LinkedIn.png'
import Pinterest from './images/Pinterest.png'
import Twitter from './images/twitterimg.png'
import Password from './images/password.png'

const useStyles = makeStyles(theme => ({
  root: {
    padding: theme.spacing(3, 2),
    width: '166%',
    height:'100px',
    fontSize: 'small',
  },
}));

export default function PaperSheet() {
  const classes = useStyles();

  return (
    <div className = "userBar">
      <Paper className={classes.root}>
      <div className="userImg">
    <img src={User} alt="User" />
    </div>
    <p className="name">Selena Gomez<br/>
        <p className="position"> CEO - Company </p>
    </p>
    <div className="vl"></div>
    <div className="mail">
    <MailIcon/><p>selenamail@mail.com</p>
    </div>

    <div className="phone">
    <PhoneIcon/><p>+123 456 789</p>
    </div>

    <div className="phone">
    <LocationOnIcon/><p>California, USA</p>
    </div>

    <div className="vl"></div>

    <a href="https://www.facebook.com"><img src={Facebook} alt="Facebook" /></a>
    <a href="https://www.linkedin.com"><img src={LinkedIn} alt="LinkedIn" /></a>
    <a href="https://www.pinterest.com"><img src={Pinterest} alt="Pinterest" /></a>
    <a href="https://www.twitter.com"><img src={Twitter} alt="Twitter" /></a>

    <div className="vl"></div>

    <div className="contactButton">
    <a className="button">Contact</a>
    </div>

    <div className="vl"></div>

    <div className="password">
    <img src={Password} alt="password"/>
    <a> Change Password</a>
    </div>




      </Paper>
    </div>
  );
}
