import React from 'react';
import { makeStyles } from '@material-ui/core/styles';
import Table from '@material-ui/core/Table';
import TableBody from '@material-ui/core/TableBody';
import TableCell from '@material-ui/core/TableCell';
import TableHead from '@material-ui/core/TableHead';
import TableRow from '@material-ui/core/TableRow';
import Paper from '@material-ui/core/Paper';

const useStyles = makeStyles(theme => ({
  root: {
    width: '195%',
    height:'700px',
    marginTop: theme.spacing(3),
    overflowX: 'auto',
  },
  table: {
    minWidth: 650,
  },
}));

function createData(weight, freezer, selection, product, packages) {
  return { weight, freezer, selection, product, packages };
}

const rows = [
      createData('10kg', 'Freezer1', '12 July 2019', 'Product Name', '10'),
      createData('10kg', 'Freezer1', '12 July 2019', 'Product Name', '10'),
      createData('10kg', 'Freezer1', '12 July 2019', 'Product Name', '10'),
      createData('10kg', 'Freezer1', '12 July 2019', 'Product Name', '10'),
      createData('10kg', 'Freezer1', '12 July 2019', 'Product Name', '10'),
      createData('10kg', 'Freezer1', '12 July 2019', 'Product Name', '10'),
      createData('10kg', 'Freezer1', '12 July 2019', 'Product Name', '10'),
      createData('10kg', 'Freezer1', '12 July 2019', 'Product Name', '10'),
      createData('10kg', 'Freezer1', '12 July 2019', 'Product Name', '10'),
      createData('10kg', 'Freezer1', '12 July 2019', 'Product Name', '10'),
      createData('10kg', 'Freezer1', '12 July 2019', 'Product Name', '10'),
      createData('10kg', 'Freezer1', '12 July 2019', 'Product Name', '10'),
      createData('10kg', 'Freezer1', '12 July 2019', 'Product Name', '10'),
      createData('10kg', 'Freezer1', '12 July 2019', 'Product Name', '10'),
      createData('10kg', 'Freezer1', '12 July 2019', 'Product Name', '10'),
      createData('10kg', 'Freezer1', '12 July 2019', 'Product Name', '10'),
      createData('10kg', 'Freezer1', '12 July 2019', 'Product Name', '10'),
      createData('10kg', 'Freezer1', '12 July 2019', 'Product Name', '10'),
];

export default function SimpleTable() {
  const classes = useStyles();

  return (
    <div className ="table1">
    <Paper className={classes.root}>
      <Table className={classes.table}>
        <TableHead>
          <TableRow>
            <TableCell><input type="checkbox" name="weight" value="weight"/>Weight</TableCell>
            <TableCell align="right">Freezer</TableCell>
            <TableCell align="right">Selection</TableCell>
            <TableCell align="right">Product</TableCell>
            <TableCell align="right">Total Packages</TableCell>
          </TableRow>
        </TableHead>
        <TableBody>
          {rows.map(row => (
            <TableRow key={row.weight}>
              <TableCell component="th" scope="row">
              <input type="checkbox" name="weight" value="weight"/>  {row.weight}
              </TableCell>
              <TableCell align="right">{row.freezer}</TableCell>
              <TableCell align="right">{row.selection}</TableCell>
              <TableCell align="right">{row.product}</TableCell>
              <TableCell align="right">{row.packages}</TableCell>
            </TableRow>
          ))}
        </TableBody>
      </Table>
    </Paper>
    </div>
  );
}
